import React, { useEffect, useState } from 'react'
import Businesspage from '../business_page/businesspage'
import Footcommon from '../../../component/footcommon/foot_common'
import ReactSearchBox from 'react-search-box'
import { message, Table } from 'antd'
import headers from '../../../variable/header'
import './major_inquire.css'
import axios from 'axios'
const columns = [
  {
    title: '名称',
    dataIndex: 'name',
    key: 'name'
  }, {
    title: '代码',
    dataIndex: 'code',
    key: 'code'
  }
]
export default function Minquire () {
  const [searchText, setSearchText] = useState('')
  const [majors, setMajors] = useState([])
  const handleSearch = (record) => {
    console.log(record) // 处理选择的记录
    // 在这里可以根据选择的记录进行其他操作
  }
  const filteredData = majors.filter((value) => {
    // 通过name属性进行筛选
    return value.name.toLowerCase().includes(searchText.toLowerCase()) // 将输入的值和数据源的值都转化为小写
  })
  // eslint-disable-next-line no-unused-vars
  useEffect(() => {
    async function getMinquire () {
      try {
        const response = await axios.get('http://47.237.100.43:8081/vevss/category/all', { headers })
        console.log(response)
        setMajors(response.data.data)
      } catch (error) {
        message.error('获取专业列表失败')
        console.log(error)
      }
    }
    getMinquire()
    console.log(majors)
  }, [])
  return (
    <div>
      <Businesspage name="专业库"></Businesspage>
      <div className='Minquire-content'>
        <div className="Minquire-tips">
        如您所查的门类（专业大类)、专业类、专业与专业目录有出入，请以专业目录为准。
        </div>
        <div className="Minquire-home">
          <div className="Minquire-home-search">
            <ReactSearchBox
              placeholder="搜索专业"
              data={majors}
              onSelect={handleSearch}
              value={searchText}
              onChange={(value) => setSearchText(value)}
              autoFocus
              className="Minquire-home-search-box"
            />
          </div>
          <div className="Minquire-home-content">
          <Table columns={columns} dataSource={filteredData} />
          </div>
        </div>
      </div>
      <Footcommon></Footcommon>
    </div>
  )
}
